<template>
    <div id="cheeru-group">
	  	<!-- 导航 -->
		<cheeru-banner :type="type" :index="3"></cheeru-banner>
		<div class="bgimg">
			<img :src="imageUrl" alt="">
			<div class="bgcontent">
				<pre>{{content}}</pre>
				<div class="btngroup">
					<button class="btn1" @click="scroll2">帮我安排</button>
					<button class="btn2" @click="scroll1">查看案例</button>
				</div>
			</div>
		</div>
		<div class="gcontent">
			<div class="gcontent_inset">
				<ul id="example">
					<li class="img_item" v-for="item in caselist" @click="enteritem(item)">
						<img :src="item.image_src" alt="">
						<div class="li_content">
							<p class="text_title">{{item.title}}</p>
							<p class="text_content">{{item.description}}</p>
						</div>
					</li>
					<div class="clear"></div>
				</ul>
				<button class="loadmore" v-loading.body="loading" @click="loadmore">{{btncontent}}</button>
			</div>
			<div class="step" id="form">
				<div class="step_inset">
					<ul>
						<li>
							<div class="step_num">1</div>
							<p>填写基本信息<br/>20分钟后进行回访</p>
						</li>
						<li>
							<div class="step_num">2</div>
							<p>1对1旅游规划师<br/>为您精心设计行程</p>
						</li>
						<li>
							<div class="step_num">3</div>
							<p>满意行程<br/>签约付款</p>
						</li>
						<li>
							<div class="step_num">4</div>
							<p>开心出游<br/>规划师将一直跟进</p>
						</li>
					</ul>
				</div>
			</div>
			<div class="form">
				<div class="form_inset">
					<p>填写您的需求及联系方式,<br/>接下来交给遇岛。</p>
					<div class="entericon">
						<i class="el-icon-d-arrow-right"></i>
					</div>
					<div class="form_box">
						<input type="text" class="form_item" placeholder="怎么称呼您?" v-model="name">
						<input type="text" class="form_item" placeholder="先生or女士?" v-model="sex">
						<input type="text" class="form_item" placeholder="您的联系方式?" v-model="phone" maxlength="11">
						<input type="number" class="form_item" placeholder="去几天呢?" v-model="days">
						<input type="text" class="form_item" placeholder="什么时候想去?" v-model="date">
						<textarea placeholder="备注" class="form_item form_textarea" v-model="remark"></textarea>
						<button class="submit_btn" @click="submitform">提交</button>
					</div>
				</div>
			</div>
		</div>
		<!-- 底部 -->
		<cheeru-footer></cheeru-footer>
    </div>
</template>

<script>
import banner from '../../components/common/banner'
import footer from '../../components/common/footer'
import { addCustomerData, getAllCaseData, getCaseImageData, getPartOfCaseData } from '../../api/api'
export default {
	data() {
		return {
			btncontent: '查看更多案例',
			clickjudge: true,
			caselist: [],
			loading: false,
			content: '',
			limit: 0,
			imageUrl: '',
			name: '',
			sex: '',
			phone: '',
			days: '',
			date: '',
			remark: '',
			type: 'transparent',
			number: '',
			shadow: '',
			imgarr: ['1', '2', '3', '4', '5', '6']
		}
	},
	components: {
		'cheeru-banner': banner,
		'cheeru-footer': footer
	},
	methods: {
		enteritem(item) {
			sessionStorage.setItem('caseid', item.case_id);
			window.location.href = 'casedetail.html';
		},
		getPartOfCaseData() {
			let info = {
				offset: this.limit
			}
			getPartOfCaseData(info).then((res) => {
				this.caselist = res.data.data;
			});
		},
		getCaseImageData() {
			getCaseImageData().then((res) => {
				this.imageUrl = res.data.data.image_src;
          		this.content = res.data.data.description;
			});
		},
		loadmore() {
			if (this.clickjudge === true) {
				let info = {
					offset: this.limit + 6
				};
				this.loading = true;
				getPartOfCaseData(info).then((res) => {
					if (res.data.message === '当前无案例') {
						setInterval(() => {
							this.loading = false;
						}, 1500);
						this.btncontent = '没有更多案例';
						this.clickjudge = false;
					} else {
						this.caselist = this.caselist.concat(res.data.data);
						this.loading = false;
						this.limit += 6;
					}
				});
			} else if (this.clickjudge === false) {
				return;
			}
		},
		submitform() {
			if (this.name === '') {
				this.$message('请填写姓名');
			} else if (this.sex === '') {
				this.$message('请填写性别');
			} else if (!((this.sex === '先生') || (this.sex === '女士'))) {
				this.$message('请填写先生或女士');
			} else if (this.phone === '') {
				this.$message('请填写手机号');
			} else if (!(/^1[34578]\d{9}$/.test(this.phone))) {
				this.$message('请填写正确的手机号')
			} else if (this.days === '') {
				this.$message('请填写出行天数');
			} else if (this.date === '') {
				this.$message('请填写出行日期');
			} else if (!/^[0-9]{4}-[0-1]?[0-9]{1}-[0-3]?[0-9]{1}$/.test(this.date)) {
				this.$message('请填写正确的日期格式');
			} else {
				let info = {
					name: this.name,
					sex: this.sex,
					phone: this.phone,
					days: this.days,
					date: this.date,
					remark: this.remark
				};
				addCustomerData(info).then((res) => {
					if (res.data.code === 200) {
						this.$message({
							type: 'success',
							message: res.data.message
						});
						setInterval(() => {
		                  window.location.href = 'group.html';
		                }, 1500);
					} else {
						this.$message({
							type: 'error',
							message: res.data.message
						});
					}
				});
			}
		},
		scroll1() {
			let distance = document.getElementById('example').offsetTop;
			window.scrollTo(0, distance - 140);
			// setInterval(() => {
			// 	if (distance < document.getElementById('example').offsetTop) {
			// 		distance++;
			// 		console.log(distance);
			// 	}
			// 	window.scrollTo(0, distance - 140);
			// }, 10);
		},
		scroll2() {
			let distance = document.getElementById('form').offsetTop;
			window.scrollTo(0, distance + 100);
		},
  	bannerBG () {
  		let _this = this;
  		window.onscroll = function () {
  			let scrollTop = document.body.scrollTop || document.documentElement.scrollTop;
  			if (scrollTop >= document.getElementById('example').offsetTop - 140) {
  				_this.type = 'white';
  				_this.number = -1;
  				_this.shadow = '';
  			} else {
  				_this.type = 'transparent';
  				_this.number = 0;
  				_this.shadow = 'shadow';
  			}
  		}
  	}
  },
  created() {
  	this.getCaseImageData();
  	this.getPartOfCaseData();
  	sessionStorage.removeItem('caseid');
  },
  mounted () {
  	this.bannerBG();
  }
}
</script>

<style lang="less">
	input:-webkit-autofill {
		-webkit-box-shadow: 0 0 0px 1000px white inset;
	}
	::-webkit-input-placeholder { /* WebKit browsers */ 
		color: #999; 
	} 
	:-moz-placeholder { /* Mozilla Firefox 4 to 18 */ 
		color: #999; 
	} 
	::-moz-placeholder { /* Mozilla Firefox 19+ */ 
		color: #999; 
	} 
	:-ms-input-placeholder { /* Internet Explorer 10+ */ 
		color: #999; 
	} 
	.el-loading-mask{
		z-index: 800 !important;
	}
	#cheeru-group{
		min-width: 1200px;
	}
		.bgimg{
			width: 100%;
			height: 100%;
			position: relative;
			.bgcontent{
				position: absolute;
				top:30%;
				left: 20%;
				font-size: 36px;
				font-weight: bold;
				color:#fff;
				pre{
					font-weight: bold;
					font-family: '微软雅黑';
				}
				.btngroup{
					margin-top: 40px;
					button{
						width: 157px;
						height: 62px;
						border:1px solid transparent;
						color:#fff;
						font-size: 20px;
						cursor: pointer;
						line-height: 60px;
						text-align: center;
						margin-right: 10px;
						outline: none;
						border-radius: 2px;
						&.btn1{
							background-color:#1dd0a0;
						}
						&.btn2{
							background-color:transparent;
							border:1px solid #fff;
						}
					}
				}
			}
			img{
				width: 100%;
				min-width: 1200px;
				height: 100%;
			}
		}
		.gcontent{
			width: 100%;
			background-color: #fff;
			.gcontent_inset{
				width: 1220px;
				margin:0 auto;
				ul{
					margin-top: 100px;
					.img_item{
						width: 370px;
						height: 400px;
						margin-right: 50px;
						margin-bottom: 70px;
						cursor: pointer;
						float: left;
						&.img_item:nth-child(3n){
							margin-right: 0;
						}
						img{
							width: 100%;
							height: 280px;
							display: block;
						}
						.li_content{
							margin-top: 20px;
							letter-spacing: 1px;
							padding-left: 15px;
							padding-right: 15px;
							.text_title{
								font-weight: bold;
								width: 100%;
								overflow: hidden;
								text-overflow:ellipsis;
								white-space: nowrap;
								color:#444444;
							}
							.text_content{
								margin-top: 10px;
								font-size: 14px;
								display: -webkit-box;
								-webkit-box-orient: vertical;
								-webkit-line-clamp: 2;
								overflow: hidden;
								height: 38px;
								color:#95989a;
							}
						}
					}
					.clear{
						clear:both;
					}
				}
				.loadmore{
					width: 174px;
					height: 44px;
					margin:0 auto;
					display: block;
					color:#989b9d;
					border: 1px solid #989b9d;
					box-sizing: border-box;
					cursor: pointer;
					background-color: #fff;
					font-size: 15px;
					letter-spacing: 1px;
					margin-bottom: 80px;
					outline: none;
				}
			}
			.step{
				height: 208px;
				width: 100%;
				background-color: #f7f7f7;
				.step_inset{
					width: 1220px;
					margin:0 auto;
					ul
						li{
							width: 305px;
							height: 208px;
							float: left;
							overflow: hidden;
							.step_num{
								width: 68px;
								height: 68px;
								border-radius: 50%;
								border:1px solid #e8e8e8;
								box-sizing: border-box;
								text-align: center;
								line-height: 68px;
								color:#95989a;
								background-color: #fff;
								font-size: 18px;
								float: left;
								margin-top: 70px;
								margin-left: 30px;
							}
							p{
								float: left;
								margin-top: 83px;
								margin-left: 28px;
								color:#95989a;
								letter-spacing: 1px;
							}
						}
				}
			}
			.form{
				width: 100%;
				height: 870px;
				background-color: #fff;
				.form_inset{
					padding-top: 110px;
					position: relative;
					width: 810px;
					margin:0 auto;
					p{
						font-size: 28px;
						font-weight: bold;
						color:#1d1d1d;
						display: inline-block;
					}
					.entericon{
						width: 50px;
						height: 50px;
						border-radius: 50%;
						text-align: center;
						line-height: 50px;
						margin-top: 46px;
						display: block;
						color:#95989a;
						border:1px solid #95989a;
						box-sizing: border-box;
						text-indent: 1px;
					}
					.form_box{
						position: absolute;
						width: 550px;
						right: 0;
						top: 110px;
						.form_item{
							height: 65px;
							width: 330px;
							border:none;
							border-bottom: 1px solid #e5e5e5;
							text-align: right;
							font-size: 16px;
							outline: none;
							float: right;
							display: block;
							margin-top: 20px;
							&.form_textarea{
								resize:none;
								font-family: '微软雅黑';
								width: 550px;
								margin-top: 40px;
							}
						}
						.submit_btn{
							width: 232px;
							display: block;
							height: 60px;
							background-color: #1dd0a0;
							color:#fff;
							text-align: center;
							font-size: 16px;
							float: right;
							outline: none;
							border:none;
							margin-top: 70px;
							border-radius: 2px;
							cursor: pointer;
						}
					}
				}
			}
		}
</style>


